import React, { useEffect, useRef } from 'react';

import { gsap } from 'gsap';

import { HeadFloatCon, HeadImg } from './HeadStyle';
import { DDownDiv } from '../DropDown/DropDownStyle';

import icon from '../../img/icon.png';
import menu from '../../img/logo_menu.png';

import { sections } from '../data/Data';

export default function Header(props) {
	const logoRef = useRef(),
		menuAnRef = useRef();

	useEffect(() => {
		gsap.from(logoRef.current, {
			duration: 1,
			opacity: 0,
			left: -100,
		});
		gsap.from(menuAnRef.current, {
			duration: 3,
			opacity: 0,
			right: -500,
		});
	}, []);

	function goToTop() {
		window.scroll({ top: 0, left: 0, behavior: 'smooth' });
	}

	function renderSections() {
		return sections.map((section) => {
			let sect = section.toString();
			// if props say it's active
			if (props.active === section) {
				return (
					<p
						key={section}
						className="active"
						onClick={() => scrollToSection(sect)}>
						{section}
					</p>
				);
			} else {
				return (
					<p key={section} onClick={() => scrollToSection(sect)}>
						{section}
					</p>
				);
			}
		});
	}

	function scrollToSection(s) {
		if (s === 'About') {
			window.scrollTo({ top: 200, behavior: 'smooth' });
		} else {
			let elm = document.getElementById(s);
			elm.scrollIntoView({
				behavior: 'smooth',
				block: 'start',
				inline: 'nearest',
			});
		}
		setTimeout(() => {
			if (s === ' About') {
				window.scrollBy(0, 500);
			} else {
				window.scrollBy(0, 5);
			}
		}, 1000);
	}

	function toggleDropDown() {
		let el = document.getElementById('drop-content');
		el.classList.toggle('hidden');
	}

	return (
		<HeadFloatCon>
			<HeadImg id="Img" ref={logoRef} src={icon} onClick={goToTop} />
			<p>{props.active}</p>
			<DDownDiv>
				<div onClick={toggleDropDown} className="dropdown">
					<img
						ref={menuAnRef}
						onClick={toggleDropDown}
						alt="menu"
						src={menu}
						className="dropimg"
					/>
					<div id="drop-content" className="dropdown-content">
						{renderSections()}
					</div>
				</div>
			</DDownDiv>
		</HeadFloatCon>
	);
}
