import React, { useEffect, useRef } from 'react';

import { gsap } from 'gsap';

import { data } from '../data/Data';

import {
	ShowSubhead,
	ShowCaseCon,
	ShowH1,
	ShowImg,
	AboutTitle,
	AboutWPDiv,
} from './AboutStyle';

import meA from '../../img/RBG.jpg';

export default function About() {
	const anName = useRef(),
		anSub = useRef(),
		moreInfoRef = useRef(),
		findMoreDivRef = useRef(),
		anB = useRef();

	useEffect(() => {
		const tl = gsap.timeline();
		const delay = 2;
		const stagger = 0.5;

		tl.from(anName.current, {
			duration: delay,
			opacity: 0,
			x: -1000,
			ease: 'power4',
		});
		tl.from(anB.current, {
			duration: delay / 2,
			opacity: 0,
			y: -100,
			stagger: stagger,
			ease: 'bounce',
		});
		tl.from(anSub.current, {
			duration: delay / 2,
			opacity: 0,
			y: -100,
			stagger: stagger,
			ease: 'bounce',
		});
		tl.from(findMoreDivRef.current, {
			duration: delay / 2,
			opacity: 0,
			ease: 'bounce',
		});

		return function () {
			tl.kill();
		};
	}, []);

	return (
		<AboutWPDiv>
			<ShowCaseCon id="About">
				<AboutTitle>
					<div ref={anName} className="anName">
						<ShowH1>Hi, I'm Rick Gove</ShowH1>
					</div>
				</AboutTitle>
				<div ref={anB} className="anB">
					<ShowImg src={meA}></ShowImg>
				</div>
				<ShowSubhead ref={anSub} className="anSub">
					Junior Web Developer
				</ShowSubhead>
				<div>
					<div ref={findMoreDivRef} className="find-more">
						<div id="more-info" ref={moreInfoRef} className="hidden-data">
							<ShowSubhead>{data.about.subhead}</ShowSubhead>
							<p>{data.about.contentP1}</p>
							<p>{data.about.contentP2}</p>
							<p>{data.about.contentP3}</p>
						</div>
					</div>
				</div>
			</ShowCaseCon>
		</AboutWPDiv>
	);
}
